import React, { Component, Fragment } from 'react';
import { API, HELPER } from '../utils'

import { Header, Navigation, Cards } from '../organisms'
import { Modal } from '../molecules'
import { Badges } from '../atom'

class Home extends Component {

	state = {
		posts: [],
		modalConfig: {
			title: 'Susanto',
			links: {
				follow: 'follow_link',
				category: 'category_link',
				profile: 'profile_user_link',
				detail: 'detail_post_link'
			},
			isOpen: false,
		}
	}

	componentDidMount() {
		// console.log('Hello World!');
		API.getPostList(0).then(res => {
			console.log('REQUEST: ', res)
			this.setState({
				posts: res.data
			})
		})
	}

	handleModal = (data: any, openModal: string) => {
		// console.log('Passing Dato To Modal: ', data)
		const modalConfig = { ...this.state.modalConfig }
		if (openModal === 'open') {
			// Set Modal Config What We Want
			modalConfig.title = 'Thomi Jasir'
			modalConfig.isOpen = !modalConfig.isOpen
		} else {
			modalConfig.isOpen = !modalConfig.isOpen
		}
		this.setState({
			modalConfig
		})
	}

	render() {
		const { modalConfig, posts } = this.state
		return (
			<Fragment>
				<div className="container">
					<Header />
					<Navigation />
					{/* Section Trending */}
					<section>
						<Cards handleModal={this.handleModal}/>
					</section>
					{/* Section Video List */}
					<section>
						<Cards handleModal={this.handleModal}/>
					</section>
					{/* Section News Feed */}
					<section>
						<div className="row row-no-padding match-heigh">
							{posts.map((item: any, index: number) => (
								<div className="col-sm-12 col-md-6 col-lg-4" key={index}>
									<div className={'content-box gradient-' + HELPER.getRandomInt(0, 10)}>
										<div className="box-nav">
											<div className="box-label">
											<Badges idCategories={item.categories}/>
											</div>
											<div className="button-menu" onClick={() => this.handleModal(item.id, 'open')}>
												<div className="ripple">
													<i className="fas fa-ellipsis-v"></i>
												</div>
											</div>
										</div>

										<div className="card small">
											<div className="card-image">
												<img src={item._embedded['wp:featuredmedia'][0].source_url} alt={HELPER.parsePostTitle(item.title.rendered)} />
											</div>
											<div className="card-text ripple">
												<h2 className="title-text">{HELPER.parsePostTitle(item.title.rendered)}</h2>
												<p>{HELPER.parseExcerpt(item.excerpt.rendered)}</p>
											</div>
											<div className="card-footer">
												<div className="left-foot">
													<div className="author-time">
														<img src={item._embedded.author[0].avatar_urls['48']} alt={item._embedded.author[0].name} />
														<div className="text">
															{item._embedded.author[0].name} - {HELPER.timeAgo(item.date)}
														</div>
													</div>
												</div>
												<div className="right-foot ripple">
													<i className="fas fa-share-alt"></i>
												</div>
											</div>
										</div>

									</div> { /* END CONTENT BOX */}
								</div>
							))}
						</div>
					</section>
				</div>
				{/* EMBEDDING COMPONENT */}
				<Modal config={modalConfig} closeModal={this.handleModal} />
			</Fragment>
		);
	}
}

export default Home;
