import React, { Component } from "react";
import { API, HELPER } from '../utils'

interface IBadges {
	idCategories: Array<number>
}

class Badges extends Component<IBadges> {
	
	constructor(props: any) {
		super(props)
		this.state = {
			isLoading: false,
			badges: []
		}
	}

	setLoading = (status: boolean): void => {
		this.setState({
			isLoading: status
		})
	}

	init = async () => {
		this.setLoading(true)
		const listCat = this.props.idCategories
		const tempBadge = [];
		for(let i = 0; i < 2; i++){
			if(listCat[i]) {
				const temp = await API.getCategory(0, listCat[i])
				tempBadge.push(temp.data)
			} else {
				break;
			}
		}
		this.setState({
			isLoading: false,
			badges: tempBadge
		})
	} 

	componentDidMount(){
		this.init()
	}

	render() {
		const { badges, isLoading }: any = this.state
		if(isLoading) {
			return 'Loading...'
		}
		return (
			<div className="badges">
			{
				badges.map((item: any, index: number) => (
					<div className="badge auto ripple" key={item.id}>
						<i className="fas fa-award"></i>
						<label>{item.name}</label>
				</div>
				))
			}
			</div>
		);
	}
}

export default Badges;